@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Roboto-bold';
  src: url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Roboto-medium';
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Roboto-black';
  src: url("../fonts/Robotoblack.woff2") format("woff2"), url("../fonts/Robotoblack.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'ProximaNova';
  src: url("../fonts/ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

body.show-nav {
  overflow: hidden; }

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden; }

.wrap {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 10px; }

.show {
  display: block; }

.hide {
  display: none; }

.btn {
  border: none;
  font-family: 'Roboto-bold';
  text-decoration: none;
  text-align: center;
  color: #fff;
  font-size: 14px;
  background: #27587B;
  text-transform: uppercase;
  outline: none;
  padding: 11.5px 32px;
  border-radius: 50px;
  cursor: pointer; }

.btn--main {
  background: #0063FF;
  margin-top: 72px;
  padding-right: 55px;
  padding-left: 55px;
  box-shadow: 0px 0px 17px 9px rgba(87, 119, 232, 0.49); }

@media (max-width: 980px) {
  .wrap {
    padding: 0 10px; } }

.header {
  padding: 27px 0;
  height: 595px;
  background: url("../img/main-bg.jpg") center no-repeat;
  background-size: cover;
  position: relative; }

.header__wrap {
  display: flex;
  flex-direction: column;
  height: 100%; }

.header__top {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

.header__bottom {
  margin: auto;
  text-align: center; }

.header-title {
  font-family: "Roboto-black";
  color: #fff;
  font-size: 44px;
  line-height: 52px;
  text-transform: uppercase;
  margin: 25px 0; }

.header-subtitle {
  font-size: 22px;
  line-height: 26px;
  color: #fff;
  margin: 0; }

.header__top-name {
  text-decoration: none;
  color: inherit;
  font-size: 20px;
  font-family: "Roboto-bold";
  text-transform: uppercase;
  color: #fff; }

@media (max-width: 980px) {
  .header-title {
    font-size: 35px;
    line-height: 53px;
    margin: 15px 0; }
  .header-subtitle {
    font-size: 18px; } }

/* Nav */
.nav {
  display: flex;
  font-size: 16px;
  height: 100%; }

.nav__link {
  position: relative;
  display: flex;
  align-items: center;
  margin: 0 10px;
  display: block;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  text-transform: uppercase;
  transition: background 0.15s linear;
  border-bottom: 2px solid transparent;
  border-bottom: 2px solid transparent; }
  .nav__link:hover {
    color: #fff; }
    .nav__link:hover::after {
      content: '';
      position: absolute;
      height: 2px;
      width: 100%;
      background: #fff;
      display: block;
      left: 0;
      z-index: 3;
      margin-top: 5px; }
  .nav__link:last-child {
    margin-right: 0; }

/* Burger */
.burger {
  width: 30px;
  height: 23px;
  padding: 0;
  background: none;
  border: none;
  display: none;
  font-size: 0;
  color: transparent;
  position: relative; }

.burger:focus {
  outline: none; }

.burger__line {
  width: 30px;
  height: 2px;
  margin: 3px 0;
  background-color: #fff;
  display: block;
  transform: translateX(0);
  transition: transform 0.5s ease-in-out; }

.burger__line:nth-child(1) {
  width: 20px;
  margin-left: auto; }

.burger.active .burger__line:nth-child(1),
.burger.active .burger__line:nth-child(3) {
  transform: translateX(200%);
  transition: transform 0.2s ease-in-out; }

.burger.active .burger__line:nth-child(2),
.burger.active .burger__line:nth-child(4) {
  left: 0;
  top: 50%;
  position: absolute;
  transform-origin: center;
  transition: transform 0.7s ease-out; }

.burger.active .burger__line:nth-child(2) {
  transform: rotate(45deg); }

.burger.active .burger__line:nth-child(4) {
  transform: rotate(-45deg); }

@media (max-width: 768px) {
  /* Nav */
  .nav {
    width: 100%;
    min-height: calc(100vh - 50px);
    overflow: auto;
    padding-top: 9px;
    flex-direction: column;
    background-color: #0f3a7e;
    position: absolute;
    top: 80px;
    right: 0;
    left: 0;
    z-index: 1;
    margin-bottom: 9px;
    transform: translateX(100%);
    transition: transform 0.5s ease-in-out; }
  .nav.show {
    transform: translateX(0); }
  .nav__link {
    display: block;
    padding: 10px 20px;
    text-align: right; }
    .nav__link:hover::after {
      display: none; }
  .burger {
    display: block; } }

.main {
  background: #F3F3F3;
  padding: 60px 0 25px;
  position: relative; }

.main__title {
  text-align: center;
  margin: 0; }

.scroll {
  width: 53px;
  height: 53px;
  border-radius: 50%;
  background: #27587B;
  position: fixed;
  bottom: 200px;
  right: 150px;
  cursor: pointer; }
  .scroll .scroll-icon {
    width: 18px;
    height: 13px;
    fill: #fff;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

@media (max-width: 980px) {
  .scroll {
    right: 50px; } }

.star {
  width: 18px;
  height: 18px; }

.star--green {
  fill: #357B38; }

.star--blue {
  fill: #0F3A7E; }

.star--red {
  fill: #AC3636; }

.star--lightgreen {
  fill: #5A7D0E; }

.star--lightpurple {
  fill: #693D96; }

.categories {
  margin: 45px auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.categories__item {
  box-sizing: border-box;
  width: 215px;
  height: 40px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  color: #fff;
  opacity: .5;
  margin: 15px;
  font-family: 'Roboto';
  font-size: 18px;
  padding: 10px 11px; }
  .categories__item .star {
    margin-right: 6px; }

.green {
  background: #4CAF50; }

.blue {
  background: #27587B; }

.red {
  background: #CE5757; }

.lightgreen {
  background: #7DB10D; }

.purple {
  background: #9160C2; }

.active {
  opacity: 1; }

.articles__item {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  padding-bottom: 39px;
  border-radius: 9px;
  margin-bottom: 40px;
  background: #fff; }
  .articles__item:last-child {
    margin-bottom: 0; }

.articles__item-top {
  display: flex;
  align-items: center;
  margin-bottom: 16px; }

.articles__star {
  width: 56px;
  height: 39px;
  background: url("../img/vec.svg") top left no-repeat;
  position: relative;
  top: 0;
  left: 0; }
  .articles__star .star {
    margin: auto;
    position: absolute;
    top: 10px;
    left: 11px; }

.articles__date {
  font-family: 'Roboto-medium';
  font-size: 22px;
  color: #434343; }

.articles__item-bottom {
  padding: 0 26px;
  display: flex;
  justify-content: space-between; }
  .articles__item-bottom .btn {
    margin-left: 20px; }

.articles__left {
  max-width: 636px;
  width: 50%; }

.articles__item-img {
  border-radius: 16px;
  width: 100%; }

.articles__text {
  width: 50%;
  margin-left: 52px; }

.articles-title,
.articles-subtitle {
  color: #434343; }

.articles-title {
  font-size: 18px;
  margin: 25px 0 23px;
  font-family: 'Roboto-bold'; }

.articles-subtitle {
  font-size: 16px; }

@media (max-width: 980px) {
  .articles__item-bottom {
    flex-direction: column;
    text-align: center; }
  .articles__left {
    max-width: initial;
    width: 100%; }
  .articles__item-img {
    display: block;
    margin: 0 auto; }
  .articles__text {
    margin-left: 0;
    width: 100%; } }

.footer {
  padding: 52px 0;
  background: #0E3C5C;
  color: #fff; }

.footer__items {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

.footer__info {
  font-size: 14px;
  font-family: 'ProximaNova';
  margin: 0; }

.footer__sponsor {
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: center;
  padding: 0; }

.footer__sponsor-item {
  margin: 0;
  margin-left: 20px; }
  .footer__sponsor-item:first-child {
    margin-left: 0; }

@media (max-width: 980px) {
  .footer__items {
    flex-direction: column; }
  .footer__sponsor {
    flex-wrap: wrap; } }
